import {
	invList
} from '../../common/js/json.js';
import wmPoster from "../..//components/wm-poster/wm-poster.vue";
import QR from "../../common/js/wxqrcode.js";
import {
	pathToBase64,
	base64ToPath
} from '../../common/js/image.js';
import {
	getBill,
	getCountPan,
	passValidation,
	getinviteCount,
	getInvCode
} from '../../api/api.js';

export default {
	data() {
		return {
			inviteCode: '',
			panCount: 0 ,
			inviteCount: 0,
			valiDation: 0,
			createImg: '',
			created: false,
			imgSrc:'',
			height:""
		}
	},
	components: {
		wmPoster
	},
	methods: {
		getInvData() {
			// invList().then(data => {
			// 	this.listInfo = data;
			// 	// console.log(this.listInfo);
			// });

			getInvCode().then(data => {
				this.inviteCode = data;
				// console.log(data)
			});
			passValidation().then(data => {
				// console.log(data)
				if(data == null){
					this.valiDation = 0
				}else{this.valiDation = data}
				
				
			})
			getCountPan().then(data => {
				this.panCount = data
			})
			getinviteCount().then(data => {
				this.inviteCount = data
			})
		},
		createPoster() {
			let URL = 'http://pandla.io/images/register.html?invCode=' + this.inviteCode;
			let img = QR.createQrCodeImg(URL, {
				size: parseInt(300) //二维码大小  
			});
			base64ToPath(img).then(path => {
				// console.log(path);
				this.createImg = path;
			}).catch(error => {
				console.error("error===>", error)
			});
			this.created = true;
			// this.$refs.qrcode._makeCode()
		},
		saveImg(res) {
			this.imgSrc = res.tempFilePath
		},
		save() {
			// uni.saveImageToPhotosAlbum({
			// 	filePath: res.tempFilePaths,
			// 	success: function () {
			// 		uni.showToast({
			// 		title: "图片保存成功",
			// 		icon: "none"
			// 		});
			// 		console.log('save success');
			// 	},
			// 	fail:function(e) {
			// 		uni.showToast({
			// 		title: "图片保存失败",
			// 		icon: "none"
			// 		});
			// 	}
			// });
			var arr = []
			arr[0] = this.imgSrc,
				uni.previewImage({
					urls: arr,
					// longPressActions: {
					// 	itemList: ["保存相冊"],
					// 	success: function(data) {
					// 		uni.showToast({
					// 		title: "图片保存成功",
					// 		icon: "none"
					// 		});
					// 	},
					// 	fail: function(err) {
					// 		console.log(err.errMsg);
					// 	}
					// }
				});
				}

	},
	onLoad() {
		this.getInvData()
		uni.getSystemInfo({
			success: function(res) {
				console.log(res.windowHeight);
				this.height = res.windowHeight
			}
		});
	}
}
